body {
    font-family: Arial, sans-serif;
    background-color: #1a1f24;
    display: flex;
    margin: 0;
    justify-content: stretch;
    flex-direction: row;
}

#root {
    display: flex;
    justify-self: stretch;
    justify-content: stretch;
    flex-grow: 1;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
}

.contentWrapper {
    display: flex;
    flex-direction: row;
    justify-self: stretch;
    flex-grow: 1;
    justify-content: stretch;
    gap: 1rem;
}

input[type=text] {
    background-color: black;
    outline: none;
    border: rgba(255, 255, 255, 0.33) solid 1px;
    border-radius: 0.6rem;
    padding: 10px;
    color: white;
    font-size: 15px;
    min-width: 300px;
}

input[type=text]:focus {
    border-color: aquamarine;
}

button {
    outline: none;
    border: solid 1px;
    border-radius: 10px;
    padding: 10px;
    color: white;
    font-size: 15px;
    transition: all 0.4s;
    font-weight: bold;
    cursor: pointer;
}

button.primary {
    background-color: rgba(128, 255, 212, 0.33);
    border-color: aquamarine;
}

button.primary:hover {
    background: rgba(128, 255, 212, 1);
    color: black;
}

button.destructive {
    background-color: rgba(255, 0, 0, 0.33);
    border-color: red;
}

button.destructive:hover {
    background-color: red;
}

button:disabled {
    border-color: grey;
    background: rgba(128, 128, 128, 0.33);
}

button:disabled:hover {
    background: rgba(128, 128, 128, 0.33);
    color: white;
    cursor: auto;
}

.buttonIcon {
    color: white;
    cursor: pointer;
    transition: all 0.4s !important;
}

.buttonIcon:hover {
    color: aquamarine;
}

.inputWrapper {
    display: flex;
    flex-direction: column;
}

label {
    color: rgba(255, 255, 255, 0.5);
    font-size: 10px;
    margin: 0;
    padding: 0;
    margin-left: 0.6rem;
    margin-bottom: 0.2rem;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,mBAAmB;AACnB;IACI,8BAA8B;IAC9B,yBAAyB;IACzB,aAAa;IACb,SAAS;IACT,wBAAwB;IACxB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,qBAAqB;IACrB,wBAAwB;IACxB,YAAY;IACZ,aAAa;IACb,YAAY;IACZ,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,qBAAqB;IACrB,YAAY;IACZ,wBAAwB;IACxB,SAAS;AACb;;AAEA;IACI,uBAAuB;IACvB,aAAa;IACb,2CAA2C;IAC3C,qBAAqB;IACrB,aAAa;IACb,YAAY;IACZ,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,aAAa;IACb,iBAAiB;IACjB,mBAAmB;IACnB,aAAa;IACb,YAAY;IACZ,eAAe;IACf,oBAAoB;IACpB,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,2CAA2C;IAC3C,wBAAwB;AAC5B;;AAEA;IACI,kCAAkC;IAClC,YAAY;AAChB;;AAEA;IACI,uCAAuC;IACvC,iBAAiB;AACrB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,kBAAkB;IAClB,qCAAqC;AACzC;;AAEA;IACI,qCAAqC;IACrC,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,+BAA+B;AACnC;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,+BAA+B;IAC/B,eAAe;IACf,SAAS;IACT,UAAU;IACV,mBAAmB;IACnB,qBAAqB;AACzB","sourcesContent":["/* src/styles.css */\nbody {\n    font-family: Arial, sans-serif;\n    background-color: #1a1f24;\n    display: flex;\n    margin: 0;\n    justify-content: stretch;\n    flex-direction: row;\n}\n\n#root {\n    display: flex;\n    justify-self: stretch;\n    justify-content: stretch;\n    flex-grow: 1;\n    height: 100vh;\n    width: 100vw;\n    overflow: hidden;\n}\n\n.contentWrapper {\n    display: flex;\n    flex-direction: row;\n    justify-self: stretch;\n    flex-grow: 1;\n    justify-content: stretch;\n    gap: 1rem;\n}\n\ninput[type=text] {\n    background-color: black;\n    outline: none;\n    border: rgba(255, 255, 255, 0.33) solid 1px;\n    border-radius: 0.6rem;\n    padding: 10px;\n    color: white;\n    font-size: 15px;\n    min-width: 300px;\n}\n\ninput[type=text]:focus {\n    border-color: aquamarine;\n}\n\nbutton {\n    outline: none;\n    border: solid 1px;\n    border-radius: 10px;\n    padding: 10px;\n    color: white;\n    font-size: 15px;\n    transition: al