    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    flex: 1;
    align-items: flex-start;
    min-height: 0;
    overflow: auto;
    
}

.contentContainer {
    justify-self: stretch;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.header {
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 20px;
    justify-self: stretch;

