    flex-direction: row;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    flex-wrap: nowrap;
    justify-self: stretch;
    justify-content: center;
    gap: 4rem;
    color: white;
    margin: 1rem;
}

.header p.headerItem {
    transition: all 0.4s;
    cursor: pointer;
}
.header p.headerItem.active {
    text-decoration: underline;
    color: aquamarine;
