    flex-direction: column;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 2rem;
    padding-bottom: 1rem;
    flex-wrap: nowrap;
    justify-self: stretch;
    justify-content: space-between;
    gap: 2rem;
    margin: 1rem;
}

.menu .menuItems {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-self: stretch;
    gap: 2rem;
}

