    color: aquamarine;
}

.menu .libraryMenuItem .editIcon {
    opacity: 0;
    transition: all 0.4s;
}

.menu .libraryMenuItem:hover .editIcon {
    opacity: 0.5;
}

.menu .libraryMenuItem .editIcon:hover {
    opacity: 1;
}

