___CSS_LOADER_EXPORT___.push([module.id, `.movieCard {
    margin: 1rem;
    cursor: pointer;
}

.movieCard .thumbnailImage {
    width: 200px;
    aspect-ratio: 27/40;
    border-radius: 30px;
    box-shadow: 8px 8px 12px rgba(0, 0, 0, 0.33);
    transition: all 0.4s;
    object-fit: cover;
}

.movieCard:hover .thumbnailImage {
    transform: scale(1.03);
    box-shadow: 12px 12px 18px rgba(0, 0, 0, 0.4);
}

.movieCard .metaDataWrapper {
    display: flex;
    flex-direction: row;
}

.movieCard p.movieTitle {
    padding: 0;
    margin: 0;
    margin-left: 15px;
    font-size: 12px;
    color: white;
    flex-grow: 1;
}

.movieCard p.movieDuration {
    padding: 0;
    margin: 0;
    margin-right: 15px;
    font-size: 12px;
    color: rgba(255, 255, 255, 0.75);
    font-weight: bold;
}`, "",{"version":3,"sources":["webpack://./src/MovieCard.css"],"names":[],"mappings":"AAAA;IACI,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,YAAY;IACZ,mBAAmB;IACnB,mBAAmB;IACnB,4CAA4C;IAC5C,oBAAoB;IACpB,iBAAiB;AACrB;;AAEA;IACI,sBAAsB;IACtB,6CAA6C;AACjD;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,UAAU;IACV,SAAS;IACT,iBAAiB;IACjB,eAAe;IACf,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,UAAU;IACV,SAAS;IACT,kBAAkB;IAClB,eAAe;IACf,gCAAgC;IAChC,iBAAiB;AACrB","sourcesContent":[".movieCard {\n    margin: 1rem;\n    cursor: pointer;\n}\n\n.movieCard .thumbnailImage {\n    width: 200px;\n    aspect-ratio: 27/40;\n    border-radius: 30px;\n    box-shadow: 8px 8px 12px rgba(0, 0, 0, 0.33);\n    transition: all 0.4s;\n    object-fit: cover;\n}\n\n.movieCard:hover .thumbnailImage {\n    transform: scale(1.03);\n    box-shadow: 12px 12px 18px rgba(0, 0, 0, 0.4);\n}\n\n.movieCard .metaDataWrapper {\n    display: flex;\n    flex-direction: row;\n}\n\n.movieCard p.movieTitle {\n    padding: 0;\n    margin: 0;\n    margin-left: 15px;\n    font-size: 12px;\n    color: white;\n    flex-grow: 1;\n}\n\n.movieCard p.movieDuration {\n    padding: 0;\n    margin: 0;\n    margin-right: 15px;\n    font-size: 12px;\n    color: rgba(255, 255, 255, 0.75);\n    font-weight: bold;\n}"],"sourceRoot":""}]);
